import Vue from "vue";
import VueRouter from "vue-router";
import Home from "../views/Home.vue";
import HomeMobile from "../views/HomeMobile.vue";
const originalPush = VueRouter.prototype.push;
VueRouter.prototype.push = function push(location) {
  return originalPush.call(this, location).catch((err) => err);
};

Vue.use(VueRouter);
const pcRouters = [
  {
    path: "/",
    name: "Home",
    redirect: "/home",
  },
  {
    path: "/home",
    name: "home",
    redirect: "/home/index",
    component: Home,
    children: [
      {
        path: "motion",
        component: () =>
          import(/* webpackChunkName: "motion" */ "../views/motion/motion.vue"),
      },
      {
        name: "vote",
        path: "vote",
        component: () =>
          import(/* webpackChunkName: "vote" */ "../views/vote/vote.vue"),
      },
      {
        path: "index",
        component: () =>
          import(/* webpackChunkName: "airdrop" */ "../views/vote/airdrop.vue"),
      },
      {
        path: 'receive',
        component: () =>
        import(/* webpackChunkName: "vote" */ "../views/vote/receive.vue"),
      }
    ],
  },
];

const mobileRouters = [
  {
    path: "/",
    name: "HomeMobile",
    redirect: "/homeMobile",
    meta: {
      isMobile: true,
    },
  },
  {
    path: "/homeMobile",
    name: "homeMobile",
    redirect: "/homeMobile/index",
    meta: {
      isMobile: true,
    },
    component: HomeMobile,
    children: [
      {
        path: "motion",
        meta: {
          isMobile: true,
        },
        component: () =>
          import(
            /* webpackChunkName: "motion" */ "../views/MobilePages/motion/motion.vue"
          ),
      },
      {
        path: "postMotion",
        meta: {
          isMobile: true,
        },
        component: () =>
          import(
            /* webpackChunkName: "postMotion" */ "../views/MobilePages/motion/postMotion.vue"
          ),
      },

      {
        meta: {
          isMobile: true,
        },
        path: "receive",
        component: () =>
          import(
            /* webpackChunkName: "airdrop" */ "../views/MobilePages/vote/receive.vue"
          ),
      },

      {
        meta: {
          isMobile: true,
        },
        path: "index",
        component: () =>
          import(
            /* webpackChunkName: "airdrop" */ "../views/MobilePages/vote/airdrop.vue"
          ),
      },
      {
        name: "vote",
        meta: {
          isMobile: true,
        },
        path: "vote",
        component: () =>
          import(
            /* webpackChunkName: "vote" */ "../views/MobilePages/vote/vote.vue"
          ),
      },
    ],
  },
];

const routes = [...pcRouters, ...mobileRouters];

const router = new VueRouter({
  routes,
});

export default router;
