const control = {
  state: {
    routerKey: 0,
    footTabBarShow: true,
    hecoChain: false
  },

  mutations: {
    CHANGER_ROUTER_KEY: (state) => {
      state.routerKey += 1;
    },
    CHANGER_FOOTERBAR: (state, data) => {
      state.footTabBarShow = data;
    },
    CHANGER_HECOCHAIN: (state, data) => {
      state.hecoChain = data;
    }
  },

  actions: {
    ActionRouterKey({commit}){
      commit('CHANGER_ROUTER_KEY')
    },

    ActionFOOTERBAR({commit}, data){
      commit('CHANGER_FOOTERBAR', data)
    },

    ActionHECOCHAIN({commit}, data){
      commit('CHANGER_HECOCHAIN', data)
    }
  }
}

// 和 login.js  的区别写法
export default control;
