import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store';

import { Toast, Dialog } from "vant";
Vue.use(Toast);
Vue.use(Dialog);
import "./assets/style/vant.less";

import { baseURL } from "./config/env"; //export

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
Vue.use(ElementUI);


import "font-awesome/css/font-awesome.min.css";

// css
import './assets/style/index.less';
import './assets/style/custom.less';

// axios
import axios from "axios";
Vue.prototype.$axios = axios;
axios.defaults.timeout = 10000;
axios.defaults.baseURL = baseURL;
import web3 from "./lib/contract";
Vue.prototype.$web3js = web3;
import usdt from "./lib/contract";
Vue.prototype.$usdt = usdt;
import { getLocalStorage, setLocalStorage } from "./utils/utils";
import { isMobile } from "@/utils/utils";

// 合约地址配置文件
import ContractAddressConfig from "@/lib/contract/contractAddressConfig/contractAddress";
Vue.prototype.$contractAddress = ContractAddressConfig.main;
Vue.config.productionTip = false


router.beforeEach((to, from, next) => {
  if (isMobile()) {
    if(to.meta.isMobile){
      next();
    } else {
      next({path:'homeMobile'});
    }
  } else {
    next();
  }

})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
