<template>
  <div class="top-nav">
    <div class="logo">
      <div>
        <!-- <img src="../../src/assets/image/dove.svg" alt="" /> -->
      </div>
      <ul class="navs">
        <li v-for="(item, index) in navs" :key="index" @click="addClass(index)">
          <router-link
            :to="item.to"
            tag="span"
            :class="{ actived: index == currentIndex }"
          >
            {{ item.label }}
          </router-link>
        </li>
      </ul>
    </div>

    <div class="connect-wallent">
      <span class="connect" @click="connectWallent" v-if="!walletaccount">链接钱包</span>
      <span v-else class="account">
        {{ formartadd(walletaccount) }}
      </span>
      <span
        v-if="walletaccount"
        style="
          text-align: center;
          border: 1px solid #cff09e;
          padding: 3px 0;
          margin-top: 5px;
          border-radius: 4px;
        "
        @click="loginOut"
        >登 出</span
      >
    </div>
  </div>
</template>

<script>
import { mapState, mapMutations } from "vuex";
import { formartadd, setLocalStorage, getLocalStorage } from "@/utils/utils";
import $web3js from "@/lib/contract/web3";
import { removeLocalStorage } from '../utils/utils';
const Web3 = require("web3");
export default {
  name: "vote",
  data() {
    return {
      currentIndex: 0,
      userAccount: "",
      navs: [
        // { label: "Proposal", to: "/home/motion" },
        // { label: 'Receive Airdrop', to: '/home/receive'},
        { label: "注册领取Peth空投", to: "/home/index" }
      ],
    };
  },

  computed: {
    ...mapState("login", ["walletaccount"]),
  },

  created() {
    // this.getcurrentIndex()
    //   ? (this.currentIndex = this.getcurrentIndex())
    //   : (this.currentIndex = 0);
  },

  methods: {
    formartadd,
    getLocalStorage,
     ...mapMutations("login", ["setUserDetail", "removeUserDetail"]),

    loginOut() {
      localStorage.clear();
      this.removeUserDetail();
      this.$store.dispatch("ActionRouterKey");
       this.$router.push({
        path: "/home/index",
      });
    },

    toVote() {
      this.$router.push({
        path: "/home/vote",
      });
    },

    addClass(index) {
      this.currentIndex = index;
      this.setcurrentIndex();
    },
    //存currentIndex
    setcurrentIndex() {
      localStorage.setItem("currentIndex", this.currentIndex);
    },
    //取currentIndex
    getcurrentIndex() {
      var saveStatus = localStorage.getItem("currentIndex");
      this.currentIndex = saveStatus;
      return this.currentIndex;
    },

    // 捐赠与空投
    handlerairdrop() {
      this.$router.push({
        path: "/home/airdrop",
      });
    },

    // Connect Wallet
    async connectWallent() {
      // let web3Provider = "";
      // let web3 = "";
      // web3Provider = window.web3.currentProvider;
      // web3 = new Web3(web3Provider);
      // let chainId = await web3.eth.net.getId();

      // if (chainId != 1) {
      //   this.$message.error("Please choose Ethereum Mainnet");
      //   return;
      // }
      this.connectMetaMask();
    },

    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          this.loadingData();
        }
      });
    },
    loadingData() {
      $web3js.connectWallet().finally(() => {
        this.userAccount = $web3js.getCurrWalletAddress();
        this.setUserDetail({
          walletaccount: this.userAccount,
        });
        if (getLocalStorage("walletaccount")) {
          this.$message({
            message: "钱包链接成功",
            type: "success",
          });
          removeLocalStorage('currentIndex')
          this.$router.push({
            path: "/home/index",
          });
          this.$store.dispatch("ActionRouterKey");
        } else {
          this.$toast("Failed Wallet Connection");
        }
      });
    },
  },
};
</script>

<style lang="less" scoped>
.top-nav {
  width: 90%;
  margin: 0 auto;
  padding: 15px 0px 0px;
  display: flex;
  justify-content: space-between;
  // grid-template-columns: 1fr 1fr 1fr;

  > div > span {
    font-size: 16px;
  }
  .logo {
    display: flex;
    align-items: center;
    // width:200px;
    > div:nth-child(1) {
      display: flex;
      width: 75px;
      height: 65px;
      img {
        width: 100%;
        height: 100%;
      }
    }
    .navs {
      display: flex;
      justify-content: center;
      // margin: 0 20px 0 40px;
      font-size: 17px;
      // border-bottom: 4px solid #cff09e;
      width: 100%;
      cursor: pointer;
      li {
        margin-left: 30px;
        span {
          padding: 5px 0;
          // margin-left: 30px;
          &.actived {
            border-bottom: 4px solid #cff09e;
            font-weight: 700;
          }
        }
      }
    }
  }
  .connect-wallent {
    // background-color: rgb(247, 248, 250);
    color: #507c5c;
    display: grid;
    direction: rtl;
    align-items: center;
    padding-right: 50px;
    > span {
      display: inline-block;
      width: 120px;
    }
    cursor: pointer;
    .connect:hover {
      display: inline-block;
      text-align: center;
      // width: 150px;
      border: 1px solid #cff09e;
      padding: 5px 10px;
      border-radius: 8px;
    }
    .account {
      display: inline-block;
      width: 138px;
      padding: 5px 10px;
      border-radius: 5px;
      border: 1px solid #cff09e;
    }
  }
}
</style>
