import {
  setLocalStorage,
  getLocalStorage,
  removeLocalStorage,
} from "@/utils/utils";

const state = {
  // 余额
  balance: getLocalStorage("balance") || "",
  // metamask 账号地址
  address: getLocalStorage("address") || "",
  key:''
};

const mutations = {
  setBalance: (state, { balance }) => {
    state.balance = balance;
    setLocalStorage("balance", balance);
  }
};

export default {
  namespaced: true,
  state,
  mutations,
};
