import $web3js from '../web3.js';

const ifilVendorContract = require('./ifilVendorContract.json')

let web3 = $web3js.getWeb3();
let contractConst = {
  contract: web3 ? new web3.eth.Contract(ifilVendorContract.abi, contractAddress) : '',
} 
let contractAddress = '';
const getIfilVendorAddress = () => {
  return contractAddress;
}

const getIfilVendorContract = () => {
  return contractConst.contract;
}

const vendorActiveABS = (_prodid) => {
  return $web3js.commonSend(getIfilVendorContract().methods.activeABS, _prodid);
}

const vendorActiveABSEstimateGas = (_prodid) => {
  return $web3js.commonEstimateGas(getIfilVendorContract().methods.activeABS, _prodid);
}

const vendorWithdraw = (_prodid) => {
  return $web3js.commonSend(getIfilVendorContract().methods.withdraw, _prodid);
}

const vendorWithdrawEstimateGas = (_prodid) => {
  return $web3js.commonEstimateGas(getIfilVendorContract().methods.withdraw, _prodid);
}

export default {
  getIfilVendorAddress,
  getIfilVendorContract,
  vendorActiveABS,
  vendorActiveABSEstimateGas,
  vendorWithdraw,
  vendorWithdrawEstimateGas,
}