<template>
  <div class="login-wrap">
    <div class="login" v-if="!walletaccount" @click="connectWallent">
      <span class="connect">链接钱包</span>
    </div>
    <div class="account-wrap" v-else>
      <span>{{ formartadd(walletaccount) }}</span>
      <div class="left" @click="loginOut">
        <span>登出</span>
      </div>
    </div>
  </div>
</template>

<script>
import "amfe-flexible";
import { mapState, mapMutations } from "vuex";
import { formartadd, setLocalStorage, getLocalStorage } from "@/utils/utils";
import $web3js from "@/lib/contract/web3";
import { Dialog } from "vant";
import { isMobile } from "@/utils/utils";
import Clipboard from "clipboard";
const Web3 = require("web3");
export default {
  data() {
    return {
      userAccount: "",
      // ??????
      connectedNetwork: "",
    };
  },

  computed: {
    ...mapState("login", ["walletaccount"]),
  },

  methods: {
    getLocalStorage,
    formartadd,
    ...mapMutations("login", ["setUserDetail", "removeUserDetail"]),

    // Connect Wallet
    async connectWallent() {
      // let web3Provider = "";
      // let web3 = "";
      // web3Provider = window.web3.currentProvider;
      // web3 = new Web3(web3Provider);
      // let chainId = await web3.eth.net.getId();

      // if (chainId != 1) {
      //   this.$toast("Please choose Ethereum Mainnet");
      //   return;
      // }
      this.connectMetaMask();
    },

    connectMetaMask() {
      $web3js.connectMetaMask().then((res) => {
        if (!getLocalStorage("walletaccount")) {
          this.loadingData();
        }
      });
    },
    loadingData() {
      $web3js.connectWallet().finally(() => {
        this.userAccount = $web3js.getCurrWalletAddress();
        this.setUserDetail({
          walletaccount: this.userAccount,
        });
        if (getLocalStorage("walletaccount")) {
          this.$toast({
            message: "钱包链接成功",
          });
          setLocalStorage("currentIndex", 0);

          this.$store.dispatch("ActionRouterKey");
        } else {
          this.$toast("Failed Wallet Connection");
        }
      });
    },

    loginOut() {
      localStorage.clear();
      this.removeUserDetail();
      this.$store.dispatch("ActionRouterKey");
      this.$router.push({
        path: "/homeMobile/index",
      });
    },
  },
};
</script>

<style lang="less" scoped>
.login-wrap {
  display: flex;
  justify-content: center;
  .login {
    width: 140px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background: skyblue;
    border-radius: 10px;
    > span {
      font-size: 16px;
      color: #fff;
    }
    > span:nth-child(2) {
      display: flex;
      width: 20px;
      height: 20px;
      margin-left: 10px;
      margin-top: -3px;
      img {
        width: 100%;
        height: 100%;
        vertical-align: middle;
      }
    }
  }

  .account-wrap {
    box-sizing: border-box;
    width: 200px;
    background: #aedafd;
    height: 40px;
    border-radius: 10px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 0 10px;
    > span {
      font-size: 18px;
    }
    .left {
      display: flex;
      > span:nth-child(1) {
        font-size: 14px;
        border: 1px solid #f7f8f9;
        padding: 2px 4px;
        border-radius: 5px;
      }
    }
  }
}
</style>
