import Vue from 'vue'
import Vuex from 'vuex'
import login from './modules/login'
import commondata from './modules/commondata'
import control from './modules/control'
import getters from './getters';

Vue.use(Vuex)

export default new Vuex.Store({
    modules: {
        login,
        commondata,
        control
    },
    getters
})
