<template>
  <div id="app">
    <keep-alive>
      <router-view :key="$store.getters.routerKey" />
    </keep-alive>
  </div>
</template>

<script>
import "./assets/style/reset.css";
import {
  getLocalStorage,
  setLocalStorage,
  removeLocalStorage,
} from "./utils/utils";
import { mapState, mapMutations } from "vuex";
import $web3js from "@/lib/contract/web3";
const Web3 = require('web3')
export default {
  created() {
    let ethereum = window.ethereum;
    let _this = this;
    if (ethereum && ethereum.isMetaMask) {
      // metamask
      ethereum.on("accountsChanged", function(accounts) {
        if (accounts[0] !== getLocalStorage("walletaccount").toLowerCase()) {
          _this.setUserDetail({
            walletaccount: accounts[0],
          });
          _this.$store.dispatch("ActionRouterKey");
          _this.$message({
            message: "Please be noted you have changed the current account",
            type: "success",
          });
        }
      });
       ethereum.on("chainChanged", function(chainId) {
        _this.getChainId();
      });
    }
  },
  methods: {
    ...mapMutations("login", ["setUserDetail"]),
    async getChainId() {
      let connectedChainId = await $web3js.getchainid();
      // alert(connectedChainId)

      if (connectedChainId != 1) {
        this.$message.error("Please choose Ethereum Mainnet");
        this.$store.dispatch("ActionRouterKey");
      } else {
        this.$store.dispatch("ActionRouterKey");
      }
    }
  },
};
</script>

<style lang="less">
#app {
  width: 100%;
  height: 100%;
}
</style>
